<template>
  <div></div>
</template>
<script type="text/ecmascript-6">
export default {
  created() {
    this._gotoShareUrl()
  },
  methods: {
    _gotoShareUrl() {
      window.location.href = 'https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MjM5OTIzOTA4MA==&scene=123#wechat_redirect'
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
</style>



